.landing-grid {
  display: grid;
  grid-template-areas:  ' welcome welcome welcome   '
                        ' welcome welcome welcome   '
                        ' welcome welcome welcome   '
                        ' skills skills skills  ';
 overflow: hidden;                       
}
                      

.welcome h1 {
  font-weight: 400;
  margin: 0;
  font-size: 3em;
}

.welcome {
  grid-area: welcome;
  margin: 10%;
}



.skills {
  grid-area: skills;
  display: grid;
  grid-template-areas:  'html css ruby'
                        'js node react';
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 10%;
;
}

.skills section {
  background-color: aqua;
  border-radius: 50%;
  width: 90px;
  height: 90px;
  text-align: center;
  line-height: 90px;;
  margin: 3px;
  padding: 0;
}

.html{
  grid-area: html;
  -webkit-animation: html .5s ease-in-out;
          animation: html .5s ease-in-out; 
}

@-webkit-keyframes html {
  0% {-webkit-transform: translateX(-100px);transform: translateX(-100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

@keyframes html {
  0% {-webkit-transform: translateX(-100px);transform: translateX(-100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

.css{
  grid-area: css;
  -webkit-animation: css 1s ease-in-out;
          animation: css 1s ease-in-out;
}

@-webkit-keyframes css {
  0% {-webkit-transform: translateY(-500px);transform: translateY(-500px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

@keyframes css {
  0% {-webkit-transform: translateY(-500px);transform: translateY(-500px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

.ruby{
  grid-area: ruby;
  -webkit-animation: ruby .8s ease-in-out;
          animation: ruby .8s ease-in-out;
}

@-webkit-keyframes ruby {
  0% {-webkit-transform: translateX(100px);transform: translateX(100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

@keyframes ruby {
  0% {-webkit-transform: translateX(100px);transform: translateX(100px)}
  100% {-webkit-transform: translateX(0px);transform: translateX(0px)} 
}

.js{
  grid-area: js;
  -webkit-animation: html .9s ease-in-out;
          animation: html .9s ease-in-out; 
}

.node{
  grid-area: node;
  -webkit-animation: node 1s ease-in-out;
          animation: node 1s ease-in-out;
}

@-webkit-keyframes node {
  0% {-webkit-transform: translateY(300px);transform: translateY(300px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

@keyframes node {
  0% {-webkit-transform: translateY(300px);transform: translateY(300px)}
  100% {-webkit-transform: translateY(0px);transform: translateY(0px)} 
}

.react{
  grid-area: react;
  -webkit-animation: ruby 1s ease-in-out;
          animation: ruby 1s ease-in-out;

}




